<style>
  .order{
    height: 100vh;
    background: #eee;
    padding: 10px;
  }
  .order div.shap{
    border: 1px solid red;
    border-radius: 5px;
    height: 26vw;
    width: 95vw;
    margin: 0 auto 10px;
  }
  .order .dididi{
    width: 100vw;
    height: 100vw;
  }
</style>
<template>
  <div class="order">
    <div class="shap" v-for="(item,i) of shangpin" :key="i">价格：{{shangpin[i].price}} 尺寸：{{shangpin[i].size}} 数量：{{shangpin[i].count}}</div>
    <p v-if="shangpin.address">地址：{{shangpin.address}}</p>
    <p v-else>您还没有添加地址</p>
    <mt-button type="danger" v-if="zhifu" @click="showPopup">立即支付</mt-button>
    <mt-button v-else type="danger" @click="openaddress">添加新地址</mt-button>
    <mt-popup v-model="visible" position="bottom">
      <div class="dididi">
        <p>确认付款</p><h1>{{price}}</h1>
        <input type="password" placeholder="请输入密码"><br>
        <mt-button type="primary" @click="fukuan">确认付款</mt-button>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';

export default {
  data(){
    return {
      shangpin: [],  //商品对象
      visible: false,
      selectIndex: [],
      zhifu: true,  //是否显示支付按钮
    }
  },
  methods:{
    openaddress(){
      this.$router.push("/address")
    },
    showPopup(){
      this.visible = true
    },
    fukuan(){
      this.visible = false;
      this.$store.commit("spliceArr",this.selectIndex)
      Indicator.open('付款中...');
      setTimeout(()=>{
        Indicator.close();
        Toast("付款成功")
        this.$router.go(-1)
      },1000)
    }
  },
  computed:{
    price(){
      return this.shangpin.reduce(function(prev,item){
        prev+=item.count*item.price
        return prev;
      },0)
    }
  },
  created(){
    if(this.$route.params.price){
      // router跳转params传值
      console.log(this.$route.params)
      this.shangpin.push(this.$route.params)
    }else{
      // 购物车页跳转
      this.selectIndex = this.$route.params.selectIndex
      console.log(this.selectIndex)
      this.shangpin = this.$store.state.arr.reduce(function(prev,elem){
        if (elem.status) {
          prev.push(elem)
        }
        return prev;
      },[])
    }
    // 地址
    try {
      this.shangpin.address =JSON.parse(localStorage.address)[0]
    } catch (error) {
      this.zhifu = false
    }
  },
  mounted(){
    this.$store.commit("setBottomF")
  },
  destroyed(){
    this.$store.commit("setBottomT")
  }
}
</script>